import React, { Fragment } from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';

import './header.css';

const Header = ({ total, qty, isLoggedIn, onLogout }) => {
    return (
        <div className="header d-flex">
            <h3>
                <Link to="/">Store</Link>
            </h3>

            <ul className="d-flex">
                <li>
                    <Link to="/catalog/">Catalog</Link>
                </li>
                <li>
                    <Link to="/cart">Cart</Link>
                </li>
                <li>
                    <Link to="/secret">Secret</Link>
                </li>
                { !isLoggedIn
                  ?
                    <Fragment>
                      <li className="login">
                          <Link className="btn btn-primary" to="/login">Login</Link>
                      </li>
                      <li className="login">
                          <Link className="btn btn-primary" to="/register">Register</Link>
                      </li>
                    </Fragment>
                  :
                    <li className="login">
                        <a className="btn" href="#" onClick={ onLogout }>Logout</a>
                    </li>
                }
            </ul>

            <Link className="header-cart d-flex align-items-center" to="/cart">
              <svg xmlns="http://www.w3.org/2000/svg" width="26" height="26" fill="currentColor" className="bi bi-cart2" viewBox="0 0 16 16">
                <path d="M0 2.5A.5.5 0 0 1 .5 2H2a.5.5 0 0 1 .485.379L2.89 4H14.5a.5.5 0 0 1 .485.621l-1.5 6A.5.5 0 0 1 13 11H4a.5.5 0 0 1-.485-.379L1.61 3H.5a.5.5 0 0 1-.5-.5zM3.14 5l1.25 5h8.22l1.25-5H3.14zM5 13a1 1 0 1 0 0 2 1 1 0 0 0 0-2zm-2 1a2 2 0 1 1 4 0 2 2 0 0 1-4 0zm9-1a1 1 0 1 0 0 2 1 1 0 0 0 0-2zm-2 1a2 2 0 1 1 4 0 2 2 0 0 1-4 0z"/>
              </svg>
              <span>{ qty } items <b>{ total.toFixed(2) }$</b></span>
            </Link>
        </div>
    );
}

const mapStateToProps = ({ shoppingCart: { orderTotal, cartItems }}) => {
  return {
    total: orderTotal,
    qty: cartItems.length
  }
}

export default connect(mapStateToProps)(Header);
